<script>
import axios from "axios";

export default {
  name: "app",
  components: {},
  data() {
    return {
      $_relinkUrl: "https://rel.ink/api/links/",
      $_relinkBase: "https://rel.ink/",
      toShorten: "",
      links: [],
      errorMessage: ""
    };
  },
  methods: {
    shortenUrl(url) {
      const errors = this.checkForErrors(url);
      if (errors) return;
      axios({
        url: "https://rel.ink/api/links/",
        method: "post",
        headers: {
          "Content-Type": "application/json"
        },
        data: {
          url: url
        }
      }).then(response => {
        const hashid = response.data.hashid;
        const originalUrl = response.data.url;
        const shortenedUrl = "https://rel.ink/" + hashid;
        this.addLink({
          hashid: hashid,
          originalUrl: originalUrl,
          shortenedUrl: shortenedUrl,
          copied: false
        });
      });
    },
    checkForErrors(url) {
      const isUrlValid = this.isUrl(url);
      const isUrlEmpty = url.length == 0;

      if (isUrlEmpty) {
        this.errorMessage = "Please add a link";
        return true;
      } else if (!isUrlValid) {
        this.errorMessage = "Please enter a valid URL. e.g http://example.com";
        return true;
      } else {
        this.errorMessage = "";
        return false;
      }
    },
    clearErrors() {
      this.errorMessage = "";
    },
    addLink(link) {
      const existingHash = this.links.filter(appLink => {
        return appLink.hashid == link.hashid;
      });
      if (existingHash.length == 0) {
        this.links.push(link);
        localStorage.appLinks = JSON.stringify(this.links);
        this.toShorten = "";
      } else {
        this.errorMessage = "Link already exists. Please enter new link";
        this.toShorten = "";
      }
    },
    isUrl(str) {
      const regexp = /^(?:(?:https?|ftp):\/\/)?(?:(?!(?:10|127)(?:\.\d{1,3}){3})(?!(?:169\.254|192\.168)(?:\.\d{1,3}){2})(?!172\.(?:1[6-9]|2\d|3[0-1])(?:\.\d{1,3}){2})(?:[1-9]\d?|1\d\d|2[01]\d|22[0-3])(?:\.(?:1?\d{1,2}|2[0-4]\d|25[0-5])){2}(?:\.(?:[1-9]\d?|1\d\d|2[0-4]\d|25[0-4]))|(?:(?:[a-z\u00a1-\uffff0-9]-*)*[a-z\u00a1-\uffff0-9]+)(?:\.(?:[a-z\u00a1-\uffff0-9]-*)*[a-z\u00a1-\uffff0-9]+)*(?:\.(?:[a-z\u00a1-\uffff]{2,})))(?::\d{2,5})?(?:\/\S*)?$/;
      if (regexp.test(str)) {
        return true;
      } else {
        return false;
      }
    },
    handleCopyStatus(hashid) {
      if (hashid.lenght !== 0) {
        document.getElementById(hashid).style.backgroundColor =
          "hsl(260, 8%, 14%)";
        document.getElementById(hashid).innerHTML = "Copied";
      }
    }
  },
  mounted() {
    if (localStorage.appLinks) {
      this.links = JSON.parse(localStorage.appLinks);
    }
  }
};
</script>

<style lang="scss">
@import "variables";
/* --- Media query mixins --- */
@mixin for-sm-only {
  @media (max-width: 420px) {
    @content;
  }
}
@mixin for-md-up {
  @media (min-width: 768px) {
    @content;
  }
}
#app {
  /* --- Header --- */
  nav {
    ul {
      > li {
        width: 100%;
        font-size: 1rem;
        font-weight: bold;
        a {
          color: $gray;
          &:hover {
            color: $very-dark-violet;
          }
          &.btn:hover {
            background-color: $very-dark-violet;
          }
        }
      }
      &.login-signup {
        min-width: 200px;
        display: flex;
        justify-content: space-around;
        .btn {
          color: #fff;
        }
      }
    }
    button.navbar-toggler {
      color: $gray;
      border: none;
    }
  }

  /* --- Landing Section --- */
  .landing-section {
    height: 60vh;
    display: flex;
    align-items: center;
    background-image: url("./assets/images/illustration-working.svg");
    background-repeat: no-repeat;
    background-position: 110% 50%;
    margin-bottom: 6.25rem;
    @include for-sm-only {
      background: none;
      margin-bottom: 10rem;
    }
    .col-sm-12.col-md-4 {
      @include for-sm-only {
        order: 1;
        height: 40vh;
        background-repeat: no-repeat;
        background-image: url("./assets/images/illustration-working.svg");
        background-size: cover;
        background-position: -50% 50%;
      }
    }
    .col-sm-12.col-md-6 {
      h1 {
        font-size: 4rem;
        color: $very-dark-violet;
        & + p {
          color: $gray;
          font-size: 1.375rem;
          margin-bottom: 2rem;
        }
      }
      @include for-sm-only {
        order: 2;
        padding: 3rem;
        h1 {
          font-size: 2.5rem;
          text-align: center;
          & + p {
            text-align: center;
            font-size: 1rem;
            & + a.btn {
              display: block;
              width: 50%;
              margin: 0 auto;
            }
          }
        }
      }
    }
  }

  /* --- URLS Section ---  */
  .urls-section {
    background-color: $light-gray;
    @include for-sm-only {
      .container {
        padding: 3rem;
      }
    }
    .row {
      @include for-sm-only {
        .col-sm-12.col-md-8 {
          margin-bottom: 1.5rem;
        }
      }

      > div {
        height: 60px;
        border-radius: 0.925rem;
      }
      &.shorten-it {
        padding: 50px 0px;
        background-color: $dark-violet;
        border-radius: 10px;
        position: relative;
        top: -80px;
        margin-bottom: -60px;
        background-image: url("./assets/images/bg-shorten-desktop.svg");
        background-size: cover;
        @include for-sm-only {
          top: -100px;
          padding: 20px;
          background-image: url("./assets/images/bg-shorten-mobile.svg");
          .col-sm-12.col-md-8 {
            &.error {
              margin-bottom: 40px;
            }
          }
        }
        input,
        a.btn {
          height: 100%;
          display: flex;
          align-items: center;
        }
        input.error {
          border: 2px solid $red;
          color: $red;
          &::placeholder {
            color: $red;
          }
          & + label {
            color: $red;
            font-weight: bold;
            font-style: italic;
            font-size: 1rem;
            padding-top: 0.8rem;
          }
          @include for-sm-only {
            & + label {
              padding: 0px;
              margin: 0px;
              margin-top: 5px;
              display: block;
            }
          }
        }
        a.btn {
          border-radius: 10px;
          justify-content: center;
        }
      }
      &.shortened {
        background-color: #fff;
        border: 1px sold $gray;
        margin-top: 1rem;
        margin-bottom: 1rem;
        &:last-child {
          margin-bottom: 0px !important;
        }
        .col-7 {
          color: $very-dark-violet;
        }
        .col-3 {
          color: $cyan;
        }
        button.btn {
          border-radius: 5px;
          font-weight: bold;
          .copied {
            background-color: $very-dark-violet;
          }
        }
      }
    }
  }

  /* --- Statistics Section --- */
  .statistics-section {
    background-color: $light-gray;
    height: 80vh;
    padding: 8rem 0px;
    .title-area {
      margin-bottom: 4rem;
    }
    h2 {
      font-weight: bold;
      text-align: center;
      color: $very-dark-violet;
      & + p {
        color: $gray;
        width: 50%;
        margin: 0 auto;
      }
    }
    .stat-cards-wrapper {
      position: relative;
      > hr {
        border: 4px solid #2acfcf;
        position: absolute;
        top: calc(50% - 2px);
        z-index: 1;
        left: 20%;
        width: 60%;
      }
    }
    .stat-cards {
      display: flex;
      height: 300px;

      > hr {
        position: absolute;
      }
      .col-4 {
        z-index: 2;
        height: 200px;
        &:nth-child(1) {
          align-self: flex-start;
        }
        &:nth-child(2) {
          align-self: center;
        }
        &:nth-child(3) {
          align-self: flex-end;
        }
        .card {
          border: none;
          padding: 0px 1rem 1rem;
          .img-wrapper {
            width: 80px;
            display: flex;
            align-items: center;
            justify-content: center;
            height: 80px;
            border-radius: 60px;
            background: $dark-violet;
            position: relative;
            top: -40px;
            left: 40px;
            margin-bottom: -40px;
            > svg.card-img-top {
              width: 50%;
            }
          }
          h4 {
            font-weight: bold;
            color: $very-dark-violet;
            & + p {
              color: $gray;
              font-size: 1rem;
            }
          }
        }
      }
    }
  }

  /* --- Footer Boost Links banner --- */
  .footer-banner {
    padding: 4rem 0px;
    background-color: $dark-violet;
    background-image: url("./assets/images/bg-boost-desktop.svg");
    background-size: cover;
    h4 {
      font-weight: bold;
      font-size: 2rem;
      color: #fff;
      margin-bottom: 1.5rem;
      & + a.btn {
        height: 3rem;
        display: flex;
        width: 200px;
        margin: 0 auto;
        align-items: center;
        justify-content: center;
      }
    }
  }

  /* --- Footer --- */
  footer {
    padding: 5rem 0px;
    background-color: $very-dark-violet;
    color: #fff;
    svg.footer-logo > path {
      fill: #fff;
    }
    .col {
      h6 {
        font-weight: bold;
